import { AppDefinition } from '../models/app-store.models';

/**
 * Maps app status to PrimeNG tag severity for visual indication
 */
export function getStatusSeverity(
  status: AppDefinition['status'],
): 'success' | 'info' | 'warn' | 'danger' | 'secondary' {
  switch (status) {
    case 'approved':
      return 'success';
    case 'pendingReview':
    case 'underReview':
    case 'changesRequested':
      return 'warn';
    case 'draft':
    case 'withdrawn':
      return 'secondary';
    case 'rejected':
    case 'suspended':
      return 'danger';
    default:
      return 'info';
  }
}

/**
 * Converts camelCase status to human-readable label
 * Examples:
 *   'pendingReview' -> 'Pending Review'
 *   'approved' -> 'Approved'
 *   'changesRequested' -> 'Changes Requested'
 */
export function getStatusLabel(status: AppDefinition['status']): string {
  return status
    .replace(/([A-Z])/g, ' $1')
    .replace(/^./, (str) => str.toUpperCase())
    .trim();
}
