import { Component, input, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppStatus } from '../../models/app-store.models';
import { getStatusSeverity, getStatusLabel } from '../../utils/status.utils';

@Component({
  selector: 'app-status-badge',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './app-status-badge.component.html',
  styleUrl: './app-status-badge.component.scss',
})
export class AppStatusBadgeComponent {
  readonly status = input.required<AppStatus | string>();

  readonly label = computed(() => getStatusLabel(this.status() as AppStatus));

  readonly badgeClass = computed(() => {
    const severity = getStatusSeverity(this.status() as AppStatus);
    switch (severity) {
      case 'success':
        return 'status-approved';
      case 'info':
        return 'status-pending';
      case 'danger':
        return 'status-rejected';
      case 'warn':
        return 'status-suspended';
      default:
        return 'status-draft';
    }
  });
}
